body {
  background: #000;
  color: #fff;
  font-family: 'Inter', Arial, sans-serif;
}

:root {
  --primary: #00D100;
  --bg-dark: #000;
  --bg-light: #fff;
  --text-dark: #000;
  --text-light: #fff;
}

[data-theme="light"] {
  background: var(--bg-light);
  color: var(--text-dark);
}

[data-theme="dark"] {
  background: var(--bg-dark);
  color: var(--text-light);
}

.navbar {
  background: var(--bg-dark);
  color: var(--text-light);
}

.navbar .nav-link, .navbar .navbar-brand {
  color: var(--text-light) !important;
}

.btn-primary {
  background: var(--primary);
  border: none;
  color: var(--text-dark);
  font-weight: 600;
}

.btn-primary:hover {
  background: #00b800;
}

.hero-section {
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #000;
}

.hero-section h1 {
  font-size: 3rem;
  font-weight: bold;
}

.hero-section p {
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.footer {
  background: var(--bg-dark);
  color: var(--text-light);
  padding: 1.5rem 0 0.5rem 0;
  font-size: 0.95rem;
}

.theme-switch {
  background: none;
  border: none;
  color: var(--primary);
  font-size: 1.2rem;
  cursor: pointer;
  margin-left: 1rem;
}
